import axios from 'axios'

export interface IArtiList {
  id: Number,
  title: String
}
interface page {
  page:string
}

export const fetchArtiList = (page:page): Promise<IArtiList[]> => {
  return axios.get(`https://im3-fe.github.io/static/${page.page}/list.json`)
    .then(res => {
      if (!res.data) {
        throw new Error(res.statusText)
      }
      return res.data as Promise<IArtiList[]>
    })
}
