import {
  ActionType,
  createReducer,
  createAction
} from 'typesafe-actions'

export const PAGE_CONTROL = 'PAGE_CONTROL_SET'
interface IControl {
  nowPage:String
}

export const control =
  createAction(PAGE_CONTROL)<IControl>()

const actions = {
  control
}

type Actions = ActionType<typeof actions>
type State = { nowPage: String}

const initialState: State = { nowPage: ""}

const reducer = createReducer<State, Actions>(initialState)
  .handleAction(control, (state, action) => {
    return { ...state, nowPage: action.payload.nowPage }
  })

export default reducer