import { ActionType, createReducer, createAsyncAction } from "typesafe-actions";
import { IArticle } from "../../../hooks/api/article";

export const FETCH_ARTICLE = {
  REQUEST: "ARTICLE_FETCH_REQUEST",
  SUCCESS: "ARTICLE_FETCH_SUCCESS",
  FAILURE: "ARTICLE_FETCH_FAILURE",
};

interface IRequest {
  id: string;
  page: string;
}

interface IResponse {
  article: IArticle;
  id: string;
  page: string;
}

interface IError {
  message: string;
}

export const fetchArticle = createAsyncAction(
  FETCH_ARTICLE.REQUEST,
  FETCH_ARTICLE.SUCCESS,
  FETCH_ARTICLE.FAILURE
)<IRequest, IResponse, IError>();

const actions = {
  fetchArticle,
};

type Actions = ActionType<typeof actions>;
type State = { article: any; message: string };

const initialState: State = { article: {}, message: "" };

const reducer = createReducer<State, Actions>(initialState)
  .handleAction(fetchArticle.success, (state, action) => {
    let article2 = { ...state.article };
    if (!(action.payload.page in article2)){
      article2[action.payload.page] = {}
    }
    article2[action.payload.page][action.payload.id] = action.payload.article;
    
    return { ...state, article: article2 };
  })
  .handleAction(fetchArticle.failure, (state, action) => {
    return { ...state, message: action.payload.message };
  })
  .handleAction(fetchArticle.request, (state) => {
    return { ...state };
  });

export default reducer;
