import { useSelector, useDispatch } from 'react-redux'
import * as actions from '../store/modules/article/reducer'
import  { RootState }  from '../store'

function useArticle() {
  const dispatch = useDispatch()
  const articletState = useSelector((store: RootState) => store.article.article)

  const fetchArticle = (page:string, id:string) => {
    dispatch(actions.fetchArticle.request({page:page,id:id}))
  }

  return {
    articletState,
    fetchArticle
  }
}

export default useArticle