import {
  ActionType,
  createReducer,
  createAsyncAction
} from 'typesafe-actions'
import { IArtiList } from '../../../hooks/api/artiList';

export const FETCH_ARTILIST = {
  REQUEST: 'ARTILIST_FETCH_REQUEST',
  SUCCESS: 'ARTILIST_FETCH_SUCCESS',
  FAILURE: 'ARTILIST_FETCH_FAILURE'
}

interface IRequest {
  page: string
}

interface IResponse {
  artiList: IArtiList[]
}

interface IError {
  message: string
}

export const fetchArtiList =
  createAsyncAction(FETCH_ARTILIST.REQUEST, FETCH_ARTILIST.SUCCESS, FETCH_ARTILIST.FAILURE)<IRequest, IResponse, IError>()

const actions = {
  fetchArtiList
}

type Actions = ActionType<typeof actions>
type State = { artiList: IArtiList[], message: string }

const initialState: State = { artiList: [], message: '' }

const reducer = createReducer<State, Actions>(initialState)
  .handleAction(fetchArtiList.success, (state, action) => {
    return { ...state, artiList: action.payload.artiList }
  })
  .handleAction(fetchArtiList.failure, (state, action) => {
    return { ...state, message: action.payload.message }
  })
  .handleAction(fetchArtiList.request, (state) => {
    return { ...state }
  })

export default reducer