import { takeEvery, call, put } from "redux-saga/effects";

import { fetchArticle } from "../../../hooks/api/article";
import { FETCH_ARTICLE } from './reducer'

function* fetch(action:any) {
  const param = action.payload
  try {
    const article = yield call(fetchArticle,param)
    yield put({ type: FETCH_ARTICLE.SUCCESS, payload: { article: article, id:param.id, page: param.page } })
  } catch (e) {
    yield put({ type: FETCH_ARTICLE.FAILURE, payload: { message: e.message } })
  }
}
const saga = [
  takeEvery(FETCH_ARTICLE.REQUEST, fetch)
]
export default saga