import React, { useLayoutEffect } from "react";
import Comment from "./comment";
import useArticle from "../hooks/useArticle";
function Article({ match }: any) {
  const { articletState, fetchArticle } = useArticle();
  let comProps = {
    aTitle: "삼피의 프론트",
    page: match.params.page,
    id: match.params.id,
  };
  useLayoutEffect(() => {
    if (
      !(match.params.page in articletState) ||
      !(match.params.id in articletState[match.params.page])
    ) {
      fetchArticle(match.params.page, match.params.id);
    } else {
      document.querySelector("#title")!.innerHTML =
        articletState[match.params.page][match.params.id]["title"];
      comProps.aTitle =
        articletState[match.params.page][match.params.id]["title"];
      document.querySelector("#date")!.innerHTML =
        articletState[match.params.page][match.params.id]["date"];
      document.querySelector("#content")!.innerHTML =
        articletState[match.params.page][match.params.id]["content"];
    }
  });
  return (
    <>
      <h1>
        <span id="title"></span>
      </h1>
      <h3>
        <i className="fas fa-user-alt"></i> 삼피
        <i className="far fa-calendar-alt"></i> <span id="date"></span>
      </h3>
      <hr />
      <div id="content"></div>
      <hr />
      <Comment comProps={comProps} />
    </>
  );
}

export default Article;
