import { useSelector, useDispatch } from 'react-redux'
import * as actions from '../store/modules/inner/reducer'
import  { RootState }  from '../store'

function useInner() {
  const dispatch = useDispatch()
  const innerState = useSelector((store: RootState) => store.inner.nowPage  )

  const controlPage = (page:string) => {
    dispatch(actions.control({nowPage:page}))
  }

  return {
    innerState,
    controlPage
  }
}

export default useInner