import { takeEvery, call, put } from "redux-saga/effects";

import { fetchArtiList } from "../../../hooks/api/artiList";
import { FETCH_ARTILIST } from './reducer'

function* fetch(action:any) {
  const param = action.payload
  try {
    const artiList = yield call(fetchArtiList, param)
    yield put({ type: FETCH_ARTILIST.SUCCESS, payload: { artiList: artiList } })
  } catch (e) {
    yield put({ type: FETCH_ARTILIST.FAILURE, payload: { message: e.message } })
  }
}
const saga = [
  takeEvery(FETCH_ARTILIST.REQUEST, fetch)
]
export default saga