import React, { useLayoutEffect } from "react";
import Comment from "./comment";
import react from "../etcs/react0.json";
import webgl from "../etcs/webgl0.json";
import ui from "../etcs/ui0.json";

function Main({ match }: any) {
  let comProps = {
    aTitle: "삼피의 프론트",
    page: match.params.page,
    id: "main",
  };
  useLayoutEffect(() => {
    // DON'T EDIT BELOW THIS LINE

    if (match.params.page === "react") {
      document.querySelector("#title")!.innerHTML = react["0"];
      comProps.aTitle = react["0"];
      document.querySelector("#date")!.innerHTML = react["1"];
      document.querySelector("#content")!.innerHTML = react["2"];
    } else if (match.params.page === "webgl") {
      document.querySelector("#title")!.innerHTML = webgl["0"];
      comProps.aTitle = webgl["0"];
      document.querySelector("#date")!.innerHTML = webgl["1"];
      document.querySelector("#content")!.innerHTML = webgl["2"];
    } else {
      document.querySelector("#title")!.innerHTML = ui["0"];
      comProps.aTitle = ui["0"];
      document.querySelector("#date")!.innerHTML = ui["1"];
      document.querySelector("#content")!.innerHTML = ui["2"];
    }
  });
  return (
    <>
      <h1>
        <span id="title"></span>
      </h1>
      <h3>
        <i className="fas fa-user-alt"></i> 삼피
        <i className="far fa-calendar-alt"></i> <span id="date"></span>
      </h3>
      <hr />
      <div id="content"></div>
      <hr />
      <Comment comProps={comProps} />
    </>
  );
}

export default Main;
