import React, { useLayoutEffect } from "react";
// import CommentCon from "./CommentCon";
import { DiscussionEmbed } from "disqus-react";

function visible() {
  document.querySelector(".commentMain")!.classList.toggle("active");
}

function Comment(props: any) {
  let config = {
    url: window.location.href,
    identifier: `${props.comProps.page}${props.comProps.id}`,
    title: `${props.comProps.title}`,
    language: "en", //e.g. for Traditional Chinese (Taiwan)
  }
  useLayoutEffect(() => {
    document.querySelector("#commentBtn")!.addEventListener("click", visible);
    
    return () => {
      document
        .querySelector("#commentBtn")!
        .removeEventListener("click", visible);
      document.querySelector(".commentMain")!.classList.remove("active");
    };
  });
  return (
    <div id="comment">
      <button id="commentBtn">댓글보기</button>
      <div className="commentMain">
        {/* <CommentCon /> */}
        <DiscussionEmbed
          shortname="im3-fe-1"
          config={config}
        />
      </div>
    </div>
  );
}

export default Comment;
