import { useSelector, useDispatch } from 'react-redux'
import * as actions from '../store/modules/artiList/reducer'
import  { RootState }  from '../store'

function useArtiList() {
  const dispatch = useDispatch()
  const artiListState = useSelector((store: RootState) => store.artiList.artiList)

  const fetchArtiList = (page:string) => {
    dispatch(actions.fetchArtiList.request({page:page}))
  }

  return {
    artiListState,
    fetchArtiList
  }
}

export default useArtiList