import React, { useLayoutEffect } from "react";
// import datas from "../etcs/react.json";
import { Link } from "react-router-dom";

import useArticleList from '../hooks/useArticleList'
import useInner from '../hooks/useInner'


function List( { data, page }:any ){
  return (
    <li>
      <Link to={`/${page}/${data.id}/`}>{data.title}</Link>
    </li>
  )
}


function Sidebar({ page }: any) {
  const { artiListState, fetchArtiList } = useArticleList()
  const { innerState, controlPage } = useInner();
  useLayoutEffect(() => {
    if (page !== innerState){
      fetchArtiList(page)
      controlPage(page);
    }
    return () => {};
  });
  return (
    <>
      <h2 className="artiListHeader">
        <span>글 목록</span>
        <i className="fas fa-sort-down"></i>
      </h2>
      <ul className="artiList">
        {artiListState.map((arti:any)=>(
          <List data={arti} page={page} key={arti.id}/>
        ))}
        {artiListState.length === 0 ? <li>게시글이 없어요</li> : null}
      </ul>
    </>
  );
}

export default Sidebar;
