import axios from 'axios'

export interface IArticle {
  title: String,
  date: number,
  content: string,
}
interface where {
  id: string,
  page:string
}

export const fetchArticle= ({page, id}:where): Promise<IArticle> => {
  return axios.get(`https://im3-fe.github.io/static/${page}/${id}.json`)
    .then(res => {
      if (!res.data) {
        throw new Error(res.statusText)
      }
      return res.data as Promise<IArticle>
    })
}
