import { combineReducers } from "redux";
import { ForkEffect } from "redux-saga/effects";

import artiList from "./artiList";
import article from "./article";
import inner from "./inner";

const combineSagas = (param: { [key: string]: ForkEffect<never>[] }) =>
  function* () {
    const targetSagas = Object.values(param).flat();

    for (let i = 0; i < targetSagas.length; i++) {
      yield targetSagas[i];
    }
  };
const RootState = {
  rootReducer: combineReducers({
    artiList: artiList.reducer,
    article: article.reducer,
    inner: inner.reducer,
  }),
  rootSagas: combineSagas({ artiList: artiList.saga, article: article.saga }),
};
export default RootState;
